
.vacancy {
    padding-top: 50px;
    padding-bottom: 11%;
    .back {
        padding-left: 20px;
        svg {
            top: 11px;
            left: 0;
            transform: rotate(180deg);
        }
    }
    .container {
        position: relative;
        .apply__link {
            position: sticky;
            top: 297px;
            margin-left: auto;
            margin-right: 25px;
            @media (max-width: 1020px) {
                top: 80px;
                right: 61px;
            }
            @media (max-width: 768px) {
               top: 0;
               right: 15px;
            }
        }
    }
    &__inner {
        margin-left: 25px;
        position: relative;
        display: flex;
    }
    &__col {
        max-width: 850px;
    }
    hr {
        border-bottom: 1px solid #303030;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    margin: 70px 0 65px 0;
    }
    h2 {
    font-size: calc(20px + 26 * (100vw / 1600));
    line-height: 100%;
    color: #FFFFFF;
    margin-top: 120px;
    margin-bottom: 60px;
    @media (max-width: 1020px) {
       margin-top: 50px;
       margin-bottom: 30px;
    }
    }
    h5 {
    font-size: calc(16px + 4 * (100vw / 1600));
    line-height: 140%;
    color: #FFFFFF;
    font-weight: 400;
    margin: 20px 0 32px 0;
    }
    h6 {
        color: #fff;
        margin-bottom: 25px;
        margin-top: 65px;
        text-transform: inherit;
    }
    p {
    line-height: 160%;
    margin: 0px 0 5px;
    }
    &__list {
        margin-bottom: 60px;
        p {
            margin-left: 28px;
            margin-top: 30px;
        }
        li {
        font-size: calc(14px + 2 * (100vw / 1600));
        line-height: 160%;
        color: #A4A4A4;
        position: relative;
        padding-left: 29px;
        &::after {
            position: absolute;
            content: '';
            background: url('../img/star.svg');
            width: 9px;
            height: 9.41px;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
        }
        }
    }
}

.vacancy__modal {
    position: fixed;
    top: 0; 
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: none;
    z-index: 100;
    padding: 50px 0;
    overflow-y: scroll;
    .form {
        background: #1A1A1A;
        max-width: 1140px;
        width: 100%;
        padding: 50px 85px 50px 85px;
        margin: 0 auto;
        border: 0.5px solid #E7B900;
        position: relative;
        span {
            img {
                width: 25px;
                height: 25px;
                position: absolute;
                right: 20px;
                top: 25px;
                cursor: pointer;
                @media (max-width: 768px) {
                    width: 18px;
                    height: 18px;
                    top: 15px;
                }
            }
        }

        &__row {
            display: flex;
            flex-wrap: wrap;
        }
        &__col6 {
            width: 50%;
            padding: 0 15px;
            @media (max-width: 620px) {
               width: 100%;
            }
        }
        &__col {
            width: 100%;
            padding: 0 15px;
        }
        label {
            font-family: Montserrat, sans-serif;
            font-size: 20px;
            line-height: 30px;
            color: #FFFFFF;
            margin-bottom: 6.67px;
            display: block;
        }
        &__input, textarea {
            width: 100%;
            display: block;
            border: 1px solid #303030;
            box-sizing: border-box;
            border-radius: 0;
            font-family: Montserrat, sans-serif;
            font-size: 16px;
            line-height: 30px;
            color: #C4C4C4;
            padding-left: 23px;
            margin-bottom: 26px;
            background-color: transparent;
            text-transform: capitalize;
            &:hover, &:active {
                border: 1px solid #FFFFFF;
            }
            &:hover::placeholder, &:active::placeholder {
                color: #FFFFFF;
            }
        }

        &__input{
            height: 50px;
        }
        
        textarea {
            min-height: 120px;
        }
        .form__file {
            position: relative;
            display: flex;
            align-items: center;
            padding-left: 40px;
            &:after {
                position: absolute;
                content: '';
                top: 50%;
                transform: translateY(-50%);
                left: 0;
                background: url('../img/file.svg');
                width: 26px;
                height: 25px;
            }
            input { 
                position: absolute;
                top: 0; left: 0;
                width: 225px;
                opacity: 0;
                padding: 14px 0;
                cursor: pointer;
            }
            span {
                font-weight: bold;
                font-size: 20px;
                line-height: 34px;
                text-transform: uppercase;
                color: #E7B900;
            }
        }
        .apply__link  {
            margin: 20px auto 0 auto;
            width: 110px;
            height: 110px;
        }
        @media (max-width: 768px) {
            &__input {
                height: 45px!important;
            }
            &__input, textarea {
                margin-bottom: 15px;
                padding-left: 15px;
            }
            label {
                font-size: 16px!important;
            }
        }
        @media (max-width: 1200px) {
            background: #1A1A1A;
            max-width: 100%;
            width: 85%;
            padding: 80px 60px 40px 60px;
            margin: 0 auto;
            border: 0.5px solid #E7B900;
            &__input{
                height: 50px;
                font-size: 14px;
            }
            label {
                font-size: 18px;
            }
            span {
                font-size: 18px!important;
            }
        }
    }
}
@media (max-width: 425px) {
    .vacancy__modal .form {
        padding: 10px 0;
        width: 95%;
    }
}
 ._error{
    border: 1px solid #FF2323!important;
}
._error::placeholder {
    color: #FF2323!important;
}
.vacancy__modal-show {
    display: block;
}
._sending {
    background-color: green;
}
.error__text {
    font-family: Montserrat, sans-serif;
    font-size: 13px;
    line-height: 30px;
    color: #FF2323;
    margin-top: -20px;
    margin-bottom: 20px;
    display: none;
    }
.error__text-active {
    display: block;
}