.team {
    &__section {
        padding-top: 8.4%;
        padding-bottom: 80px;
        overflow: hidden;
    }
    &__heading {
        font-size: calc(30px + 60 * (100vw / 1600));
        line-height: 130%;
        color: #fff;
        font-weight: 700;
        text-transform: uppercase;
        text-align: center;
    }
    &__inner {
        display: flex;
        flex-wrap: wrap;
        margin-top: 110px;
        h3 {
            color: #fff;
            font-size: calc( 20px + 14 * (100vw / 1600));
            line-height: 130%;
            font-weight: 700;
            text-transform: capitalize;
            max-width: 140px;
            width: 100%;
        }
        p {
            color: inherit;
            font-size: calc( 16px + 3 * (100vw / 1600));
            line-height: 140%;
            margin-top: 0;
            @media (min-width:  1120px) {
                margin-left: 30px;
            }
        }
    }
    &__block {
        width: 50%;
        &:first-child {
            padding-left: 25px;
            display: flex;
            position: relative;
            &:after {
                position: absolute;
                content: '';
                top: 30px;
                left: -150px;
                width: 635px;
                height: 635px;
                border: 1px solid #303030;
                border-radius: 50%;
            }
        }
        &:nth-child(2) {
            padding-left: 15px;
        }
        @media (max-width: 690px) {
           width: 100%;
        }
    }
    &__img {
        display: inline-block;
        z-index: 1;
        overflow: hidden;
        &:first-child {
            max-width: 355px;
            max-height: 440px;
        }
        &:nth-child(2) {
            max-width: 232px;
            max-height: 348px;
            margin-top: 191px;
            margin-left: -81px;
            z-index: 1;
            overflow: hidden;
        }
        img {
            width: 100%;
            object-fit: contain;
        }
    }
    &__content {
        display: flex;
        margin: 17% 0px 22%;
        @media (max-width:  1120px) {
            display: block;
            margin-left: 25px;
        }
        @media (max-width: 690px) {
           margin: 40px 0;
        }
    }
}
// VALUE
.value {
    &__section {
        padding-top: 85px;
        padding-bottom: 75px;
        .section__heading {
            text-align: center;
        }
        img {
            position: absolute;
            right: 0;
            margin-top: -170px;
            width: auto;
            height: auto;
        }
    }
    &__inner {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -15px;
    }
    &__block {
        width: calc(100% / 3);
        padding: 0 15px;
        margin-bottom: 45px;
        h6 {
            margin: 10px 0 25px;
        }
        p {
            line-height: 165%;
            max-width: 90%;
            clear: both;
        }
        @media (max-width: 940px) {
            width: calc(100% / 2);
        }
        @media (max-width: 560px) {
           width: 100%;
        }
    }
    &__icon {
        @include flex-i-c;
        border: 1px solid #303030;
        width: 58px;
        height: 58px;
        border-radius: 50%;
        transition: .5s ease-in-out;
        svg {
            width: 25px;
            height: 25px;
            fill: $yellow;
            transition: .5s ease-in-out;
        }
        &:hover {
            background-color: $yellow;
            border-color: $yellow;
            svg {
                fill:#303030
            }
        }
        @media (max-width: 560px) {
            float: left;
            margin-right: 20px;
            margin-bottom: 15px;
        }
    }
}
// WORKPLACE
.workplace {
    &__section {
        position: relative;
        padding-top: 7.5%;
        overflow: hidden;
        svg {
            width: 390px;
            height: 334px;
            fill: #FFCC00;
            z-index: 1;
            @media (max-width: 620px) {
               width: 225px;
            }
        }
        .svg-circle {
            position: absolute;
            content: '';
            left: 20px;
            top: 19%;
            width: 614.02px;
            height: 614.02px;
            border: 1px solid #FFCC00;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            z-index: 1;
            @media (max-width: 1440px) {
                width: 530px;
                height: 530px;
                left: 0;
                svg {
                    width: 320px;
                    height: 334px;
                }
            }
            @media (max-width: 768px) {
               width: 450px;
               height: 450px;
               top: 0;
               right: 0;
               left: auto;
               svg {
                width: 215px;
                height: 260px;
            }
            }
            @media (max-width: 956px) {
               top: 0;
            }
        }
    &:after {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        right: 8%;
        height: 100%;
        background-color: $yellow;
        @media (max-width: 1415px) {
           width: 100%;
        }
    }
    }
    &__inner {
        position: relative;
        z-index: 2;
        display: flex;
        flex-wrap: wrap;
        padding-bottom: 5.5%;
        .section__heading {
            text-align: left;
            color: $main-clr;
            max-width: 582.6px;
            margin-bottom: 70px;
        }
        .workplace__block {
            max-width: 360px;
            width: 35%;
            margin-bottom: 5%;
            &:nth-child(1) {
                margin-top: -11px;
            }
            &:nth-child(3) {
                margin-right: 60px;
                @media (max-width: 920px) {
                   margin-right: 0;
                }
            }
            p {
                max-width: 321.71px;
            }
            h6 {
                line-height: 140%;
            }
            h6, p {
                color: $main-clr;
            }
            @media (max-width: 920px) {
               width: 50%;
               max-width: 100%;
               margin-left: 0;
               padding-right: 15px;
            }
            @media (max-width: 630px) {
               width: 100%;
            }
        }
    }
}

// BENEFIT
.benefit {
    &__section {
        padding-top: 8%;
        .section__heading {
            max-width: 750px;
            text-align: center;
            margin: 0 auto 8.5% auto;
        }
    }
    &__inner{
        display: flex;
        flex-wrap: wrap;
        padding-bottom: 10.2%;
        border-bottom: 1px solid #303030;
    }
    &__block {
        width: 50%;
        &:nth-child(2) {
            @media (max-width: 1030px) {
                padding-top: 30%;
            }
        }
        @media (max-width: 680px) {
            width: 100%;
         }
    }
    &__list {
        padding-left: 5%;
        li {
            display: flex;
            align-items: center;
            margin-bottom: 25px;
            h6 {
                color: #EEEEEE;
                white-space: break-spaces;
            }
        }
    }
    &__icon {
        @include flex-i-c;
        width: 109px;
        height: 109px;
        border: 1px solid #303030;
        border-radius: 50%;
        margin-right: 18px;
        transition: .5s ease-out;
        svg {
            width: 38px;
            height: 33px;
            fill: #FFCC00;
            transition: .5s ease-out;
        }
        @media (max-width: 1080px) {
            width: 85px;
            height: 85px;
            flex: 0 0 85px;
            svg {
                width: 28px;
                height: 25px;
            }
        }
        @media (max-width: 768px) {
            width: 55px;
            height: 55px;
            flex: 0 0 55px;
            svg {
                width: 20px;
            }
        }
    }
}

// VACANCIES
.vacancies {
    &__section {
        padding-top: 6.2%;
        padding-bottom: 8.9%;
        .section__heading {
            margin-bottom: 70px;
        }
    }
    &__list {
        max-width: 750px;
        margin: 0 auto;
        li {
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-bottom: 1px solid #303030;
            padding: 12px 0;
            &:last-child{
                border-bottom: 0;
            }
            h6 {
                margin: 0 0 15px 0;
            }
            .right__block {
                width: 109.35px;
                height: 109.35px;
                border: 1px solid #303030;
                border-radius: 50%;
                @include flex-i-c;
                cursor: pointer;
                transition: .3s ease-out;
                span {
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 34px;
                    text-align: center;
                    color: $yellow;
                    text-transform: uppercase;
                }
                &:hover {
                    background-color: #E7B900;
                    span {
                        color: #303030;
                    }
                }
                @media (max-width: 768px) {
                   width: 95px;
                   height: 95px;
                }
                @media (max-width: 509px) {
                    width: 80px;
                    height: 80px;
                }
                @media (max-width: 409px) {
                    width: 65px;
                    height: 65px;
                    span {
                        font-size: 11px;
                    }
                }
            }
        }

    }
    &__link {
        color: $yellow;
        font-size: calc(14px + 6 * (100vw / 1600));
        font-weight: 700;
        line-height: 34px;
        text-transform: uppercase;
        position: relative;
        display: inline-block;
        padding-right: 22px;
        transition: .3s ease-out;
        svg {
            width: 14px;
            height: 14px;
            fill: #FECC02;
            position: absolute;
            top: 11px;
            right: 0;
        }
        &:hover {
            color: #fff;
            svg {
                fill: #fff;
                transform: rotate(-45deg);
            }
        }
        @media (max-width: 678px) {
           line-height: 26px;
           padding-right: 17px;
           svg {
               width: 12px;
               height: 10px;
           }
        }
    }
   

}

// CLIENTS
.clients {
    &__section {
        padding-bottom: 2.1%;
        padding-top: 7%;
        h5 {
            margin-left: 14%;
            color: #fff;
            font-size: 24px;
            font-weight: 700;
            line-height: 30px;
            margin-bottom: 22px;
        }
    }
    &__logo {
        display: flex;
        border-top: 1px solid #303030;
        border-bottom: 1px solid #303030;
        border-left: 1px solid #303030;
        height: 130px;
        justify-content: center;
        align-items: center;
        img {
            object-fit: contain;
            width: auto;
        }
        @media (max-width: 560px) {
           height: 100px;
        }
    }
}

// Director section
.director__section {
    position: relative;
    z-index: 1;
    overflow: hidden;
  }
  .director__section .container {
    background: #E7B900;
  }
  .director__section:after {
    position: absolute;
    content: '';
    top: 0;
    left: 10.8%;
    width: 100%;
    background: #E7B900;
    z-index: -1;
    height: 100%;
  }
  .director__row {
    display: flex;
  }
  .director__content {
    position: relative;
    width: auto;
    padding-top: 6.5%;
    padding-right: 5%;
  }
  .director__img {
    flex: 0 0 482px;
    padding: 80px 99px 0 25px;
    margin-bottom: 65px;
  }
  .quote {
    position: absolute;
    content: '';
    bottom: 147px;
    right: 30px;
    width: 14em;
    height: 11.3em;
  }
  @media (max-width: 1024px) {
    .director__img {
        flex: 0 0 380px;
        padding: 80px 70px 0 25px;
        margin-bottom: 65px;
      }
      .quote {
        bottom: 20px;
        width: 10.5em;
        height: 8.3em;
    }
    .director__section {
        padding: 0 0 50px 0!important;
        background: #E7B900;
    }
  }

  .director__section h4{
    font-family: Montserrat, sans-serif;
  font-weight: 700;
  font-size: calc(22px + 11 * (100vw / 1600));
  line-height: 125%;
  color: #1A1A1A;
  margin: 34px 0px;
  position: relative;
  z-index: 1;
  }
  .director__content a {
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  line-height: 26px;
  color: #000000;
  position: relative;
  padding-left: 55px;
  z-index: 2;
    &::after {
        position: absolute;
        content: '';
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        background-color: #1A1A1A;
        width: 48.26px;
        height: 1px;
    }
  }

  @media (max-width: 768px) {
    .director__img {
        flex: 0 0 290px;
        padding: 80px 30px 0 25px;
    }
    .director__section h4 {
        font-size: 22px;
        margin-bottom: 20px;
    }
    .quote {
        bottom: 20px;
        width: 5.5em;
        height: 4.3em;
    }
    .director__content a {
        font-size: 14px;
        padding-left: 27px;
        &:after {
            width: 25px;
        }
    }
  }
  @media (max-width: 560px) {
    .director__row {
        flex-wrap: wrap;
        justify-content: center;
    }
    .director__img {
        flex: 0 0 80%;
        margin-bottom: 0;
    }
  }
  .out-1 {
      margin: 30px;
      border: 2px solid green;
  }
