body {
    height: 100vh;
}
.learn {
    background-color: #FFFFFFFF;
    display: flex;
    .box1, .box2 {
        width: 50%;
        height: 150px;
        margin: 50px 20px;
        border: 1px solid green;
        cursor: pointer;
    }
    .img {
        width: 50px;
        height: 70px;
    }
}

.anim {
    animation: spin 4s linear infinite;
}
@-webkit-keyframes spin {
	100% {
        transform: rotate(360deg);
    }
}