.footer {
    background-color: $yellow;
    padding-top: 5.2%;
    height: 500px;
    .container {
        max-width: 1270px;
    }
    &__inner {
        padding-bottom: 5.5%;
    }
    &-bottom__inner {
        padding-bottom: 38px;
            p {
                font-size: 14px;
                font-weight: 400;
                line-height: 34px;
                color: $main-clr;
                @media (max-width: 560px) {
                   margin: 0;
                }
        }
    }
    &__inner,
    &-bottom__inner {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    &__col {
        width: calc(100% / 2);
        display: flex;
        ul li {
            line-height: 34px;
            @media (max-width: 425px) {
               line-height: 30px;
            }
        }
        @media (max-width: 1080px) {
           &:nth-child(1) {
               width: 30%;
           }
           &:nth-child(2) {
            width: 70%;
        }
        }
        @media (max-width: 560px) {
           width: 100%!important;
        }
    }
    &__menu {
        padding-left: 18.5%;
        @media (max-width: 560px) {
           padding-left: 0;
        }
    }
    &__contacts {
        padding-left: 10.5%;
        @media (max-width: 560px) {
           padding-left: 0;
        }
    }
    &__menu,
    &__contacts {
        width: 50%;
        margin-top: 18px;
        @media (max-width: 768px) {
            padding-left: 0;
        }
        @media (max-width: 560px) {
           width: 40%;
        }
    }
    .menu__link, .contact__link {
        font-size: 16px;
        font-weight: 400;
        line-height: 140%;
        color: $main-clr;
        text-transform: capitalize;
        @media (max-width: 425px) {
           font-size: 14px;
        }
    }

    &__social-list {
        padding-top: 14%;
        display: flex;
        margin-bottom: 25px;
        flex-wrap: wrap;
        li {
            @include flex-i-c;
            margin-right: 30px;
            @media (max-width: 768px) {
               margin-right: 15px;
            }
            @media (max-width: 375px) {
               margin-right: 5px;
            }
        }
    }
    &__social-link {
        width: 30px;
        height: 20px;
        display: inline-block;
        svg {
            width: 100%;
            height: 100%;
            fill: #000;
            &:hover {
                fill: $yellow;
            }
        }
        
    }
}
