$main-clr: #1A1A1A;
$txt-clr: #A4A4A4;
$yellow: #E7B900;

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,700;1,600&display=swap');

body,
html {
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 120%;
    color: $txt-clr;
    
}
*{
    box-sizing: border-box;
}
*:focus{
    outline: none;
}
a {
    text-decoration: none;
    cursor: pointer;
}
h1,
h2,
h3,
h4,
h5,
h6 + p {
    margin-top: 0;
}
p {
    margin: 15px 0;
}
img {
    max-width: 100%;
    width: 100%;
    object-fit: cover;
}
ul {
    margin: 0;
    padding: 0;
    li {
        list-style: none;
    }
}
svg {
    transition: .3s;
}
button {
    border: none;
}
button,
input,
select,
textarea {
    font-family: inherit;
}
.container {
    max-width: 1220px;
    padding: 0 15px;
    margin: 0 auto;
    &-lg {
      max-width: 1170px;
  }
}
.container-fluid {
    max-width: 100%;
    padding: 0;
}
.section__heading {
  font-size: calc(30px + 40 * (100vw / 1600));
  line-height: 100%;
  color: #fff;
  font-weight: 700;
  text-align: center;
  margin-bottom: 80px;
  &._active {
    @include anim;
    animation-delay: .5s;
  }
}
h6 {
  font-size: calc(16px + 10 * (100vw / 1600));
  line-height: 130%;
  color: #fff;
  font-weight: 700;
  text-transform: capitalize;
  margin: 10px 0 20px;
}
p {
  font-weight: 400;
  font-size: 16px;
  color: inherit;
  line-height: 140%;
}
.ml-auto {
  margin-left: auto;
}

@media (max-width: 768px) {
   section {
     padding: 100px 0 40px 0!important;
   }
}
@media (max-width: 425px) {
  section {
    padding: 60px 0 20px 0!important;
  }
  p {
    font-size: 14px;
  }
}

.apply__link {
  width: 165px;
  height: 165px;
  background: #E7B900;
  border-radius: 100%;
  font-size: 14px;
  line-height: 34px;
  text-align: center;
  text-transform: uppercase;
  color: #1A1A1A;
  display: flex;
  justify-content: center;
  align-items: center;
  @media (max-width: 1020px) {
      width: 130px;
      height: 130px;
  }
  @media (max-width: 768px) {
     width: 100px;
     height: 100px;
  }
  &:hover {
    background-color: #E7B900;
    span {
        color: #303030;
    }
}
}