.logo {
    display: inline-block;
    margin-right: auto;
    width: 187px;
    height: 32px;
    @media (max-width: 768px) {
        height: 26px;
        width: 150px;
        margin: 15px 0;
    }
}
.header {
    position: relative;
    background-color: $main-clr;
    border-bottom: 1px solid #535353;
    transition: all .5s ease-in;
    &__inner {
        display: flex;
        align-items: center;
    }
    &__container {
        max-width: 1274px;
    }
.menu {
    margin-left: auto;
    &__list {
        @include flex-b-c;
        li {
            padding: 32px 28px;
            transition: .3s ease-out;
            position: relative;
            &:after {
                position: absolute;
                content: '';
                top: 0;
                left: 0;
                width: 100%;
                height: 0;
                background-color: $yellow;
                transition: .3s ease-out;
            }
            &:hover {
                .menu__link {
                    color: $main-clr;
                    z-index: 1;
                    position: relative;
                }
                &:after {
                    height: 100%;
                   
                }
            }
            &:last-child {
                padding-right: 0!important;
                &:after {
                    padding-left: 26px;
                }
            }
        }
        @media (max-width: 768px) {
           position: absolute;
           left: 0;
           right: 0;
           top: 100%;
           flex-direction: column;
           justify-content: start;
           background-color: $yellow;
           width: 100%;
           height: 100vh;
           z-index: 10;
           padding: 20px 15px;
           transform: translateX(-100%);
           opacity: 0;
           transition: all .3s ease-in;
        }

    }
    &__link {
        @include flex-i-c;
        font-size: 13px;
        line-height: 16px;
        color: #ffffff;
        font-weight: 400;
        text-transform: uppercase;
        transition: all .3s ease-in;
        @media (max-width: 768px) {
           font-size: 20px;
        }
    }
}


.menu__list-open {
    transform: translateX(0);
    opacity: 1;
    transition: all .8s ease-in-out;
}
.menu__icon-close {
     span {
        transition: all .3s ease-in-out;
        &:nth-child(1){
            transform: rotate(-45deg);
            top: 6px!important;
        }
        &:nth-child(2){
            opacity: 0;
            left: -50px;
        }
        &:nth-child(3){
            transform: rotate(45deg);
            bottom: 8px!important;
        }
    }
}

@media (max-width: 768px) {
    .menu__icon {
        width: 22px;
        height: 16px;
        margin-left: 15px;
        z-index: 10;
        transition: .5s ease-in-out;
        transform: rotate(0);
        cursor: pointer;
        span {
            position: absolute;
            left: 0;
            width: 100%;
            height: 2px;
            border-radius: 2px;
            background: $yellow;
            display: block;
            opacity: 1;
            transition: .25s ease-in-out;
            transform: rotate(0);
            &:nth-child(1) {
                top:  0;
            }
            &:nth-child(2) {
                top: 50%;
                transform: translateY(-50%);
            }
            &:nth-child(3) {
                bottom: 0;
            }
        }
    }
}}


