.gallery {
    &__wrap {
        position: relative;
        width: 100%;
        height: 400px;
        img {
            width: 100%;
            height: 100%;
            border-radius: 50%;
            transition: .3s ease-out;
            }
    }
    &__video {
        position: relative;
        width: 220px;
        height: 220px;
        top: 210px;
        left: 223px;
        border-radius: 50%;
        transform: translate(-50%,-50%);
        img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        transition: .3s ease-out;
        }
    }
    &__img {
        position: absolute;
        border-radius: 50%;
        object-fit: cover;
        &:nth-child(1) {
            width: 151px;
            height: 151px;
            top: -10px;
            left: 23px;
        }
        &:nth-child(2) {
            width: 86px;
            height: 86px;
            top: -12px;
            left: 273px;
        }
        &:nth-child(3) {
            width: 114.61px;
            height: 114.61px;
            top: 40px;
            left: 400px;
            @media (max-width: 610px) {
               left: 60%;
               top: 90%;
            }
        }
        &:nth-child(4) {
            width: 130.8px;
            height: 130.8px;
            top: 202px;
            left: 318px;
            z-index: 1;
            @media (max-width: 910px) {
                top: 149px;
                left: 10px
            }
            @media (max-width: 680px) {
                top: 210px;
                left: 295px;
            }
        }
        &:nth-child(5) {
            width: 161.39px;
            height: 161.39px;
            top: 237px;
            left: 25px;
            z-index: 1;
            @media (max-width: 1120px) {
               top: 280px;
            }
        }
    }

}
@keyframes spin {
    0% { transform: translate(-50%, -50%) scale(1.5) rotate(0deg); }
    100% { transform: translate(-50%, -50%) scale(1.5) rotate(360deg); }
  }
  @keyframes big-spin {
    0% { transform: translate(-50%, -50%) scale(2.5) rotate(0deg); }
    100% { transform: translate(-50%, -50%) scale(2.5) rotate(360deg); }
  }
  .gallery__img:nth-child(3):hover img, 
  .gallery__img:nth-child(4):hover img {
      transform: scale(2);
  }
  .gallery__img:nth-child(3), 
  .gallery__img:nth-child(4){
    .icon__team-circle {
        animation: big-spin 8s linear infinite;
    }
  }
.gallery__video, .gallery__img {
    transition: .5s ease-out;
    overflow: visible!important;
    &:hover {
        z-index: 2;
        img {
            transform: scale(1.2);           
        }
        .icon__team-circle {
            opacity: 1;
        }
    } 
    .icon__team-circle {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 100%;
        fill: $yellow;
        opacity: 0;
        transition: .5s ease-out;
        animation: spin 8s linear infinite;
        @media (max-width: 768px) {
            display: none;
        }
}
}


.icon__video-arrow {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 30px;
    height: 40px;
    transform: translate(-50%, -50%)!important;
    cursor: pointer;
    fill: #FFCC00;
    transition: .3s ease-out;
    z-index: 10;
}

.video-modal {
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.7);
    z-index: 1000;
    justify-content: center;
    align-items: center;
  }
  .modal__wrap {
    width: 70%;
    margin: 0 auto;
  }
  .video-modal video {
    width: 70%;
    display: block;
    margin: 100px auto 0 auto;
  }
  ._active {
    display: flex!important;
  }
  .close__wrap {
    width: 70%;
    margin: 65px auto 0 auto;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    }
  .close__icon {
    position: relative;
    width: 27px;
    height: 27px;
    display: flex;
    margin-left: auto;
    margin-right: -40px;
    top: 13px;
    cursor: pointer;
  }
  .close__icon:after{
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: #E7B900;
    transform: rotate(45deg);
  }
  .close__icon:before{
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: #E7B900;
    transform: rotate(-45deg);
  }
  ._active {
      display: block;
  }

  @media (max-width: 455px) {
    .gallery__video {
        width: 180px;
        height: 180px;
        top: 174px;
        left: 225px;
    }
    .gallery__img:nth-child(5) {
        top: 160px;
        left: 0;
        height: 150px;
        width: 150px;
    }
    .gallery__img:nth-child(4) {
        top: 231px;
    left: 186px;
    }
    .gallery__img:nth-child(3) {
        top: 83%;
        left: 75px;
    }
    .gallery__img:nth-child(2) {
        left: 215px;
    }
    
  }
  @media (max-width: 346px) {
    .gallery__img:nth-child(4) {
        left: 140px;
        width: 120px;
        height: 120px;
    }
    .gallery__video {
    width: 130px;
    height: 130px;
    top: 174px;
    left: 200px;
    }
    .gallery__img:nth-child(2) {
        left: 180px;
    }
  }