.header {
  position: relative;
  background-color: #1A1A1A;
  border-bottom: 1px solid #535353;
  transition: all .5s ease-in;
}

.header__inner {
  display: flex;
  align-items: center;
}

.header__container {
  max-width: 1274px;
}

.header .menu {
  margin-left: auto;
}

.header .menu__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .menu__list li {
  padding: 32px 28px;
  transition: .3s ease-out;
  position: relative;
}

.header .menu__list li:after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  background-color: #E7B900;
  transition: .3s ease-out;
}

.header .menu__list li:hover .menu__link {
  color: #1A1A1A;
  z-index: 1;
  position: relative;
}

.header .menu__list li:hover:after {
  height: 100%;
}

.header .menu__list li:last-child {
  padding-right: 0 !important;
}

.header .menu__list li:last-child:after {
  padding-left: 26px;
}

@media (max-width: 768px) {
  .header .menu__list {
    position: absolute;
    left: 0;
    right: 0;
    top: 100%;
    flex-direction: column;
    justify-content: start;
    background-color: #E7B900;
    width: 100%;
    height: 100vh;
    z-index: 10;
    padding: 20px 15px;
    transform: translateX(-100%);
    opacity: 0;
    transition: all .3s ease-in;
  }
}

.header .menu__link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  line-height: 16px;
  color: #ffffff;
  font-weight: 400;
  text-transform: uppercase;
  transition: all .3s ease-in;
}

@media (max-width: 768px) {
  .header .menu__link {
    font-size: 20px;
  }
}

.header .menu__list-open {
  transform: translateX(0);
  opacity: 1;
  transition: all .8s ease-in-out;
}

.header .menu__icon-close span {
  transition: all .3s ease-in-out;
}

.header .menu__icon-close span:nth-child(1) {
  transform: rotate(-45deg);
  top: 6px !important;
}

.header .menu__icon-close span:nth-child(2) {
  opacity: 0;
  left: -50px;
}

.header .menu__icon-close span:nth-child(3) {
  transform: rotate(45deg);
  bottom: 8px !important;
}

@media (max-width: 768px) {
  .header .menu__icon {
    width: 22px;
    height: 16px;
    margin-left: 15px;
    z-index: 10;
    transition: .5s ease-in-out;
    transform: rotate(0);
    cursor: pointer;
  }
  .header .menu__icon span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    border-radius: 2px;
    background: #E7B900;
    display: block;
    opacity: 1;
    transition: .25s ease-in-out;
    transform: rotate(0);
  }
  .header .menu__icon span:nth-child(1) {
    top: 0;
  }
  .header .menu__icon span:nth-child(2) {
    top: 50%;
    transform: translateY(-50%);
  }
  .header .menu__icon span:nth-child(3) {
    bottom: 0;
  }
}

